import {mapState} from 'vuex'
export default {
	data() {
		return {
			disabled:true,
			url:'',
			coverimg:'',
			explain:'',
			videourl:'',
			playvideo:false,
			showsavebutton:false
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		inputurl:function(e){
			var url = e.detail.value
			if(url != ''){
				this.disabled = false
			}else{
				this.disabled = true
			}
			this.url = url
			console.log(url)
		},
		clearinputfn:function(){
			this.url = ''
			this.disabled = true
		},
		qushuiyinfn:function(){
			uni.showLoading({
				title:'正在去除水印',
				mask:true,
			})
			var _this = this;
			var url = encodeURI(_this.url)
			console.log(url)
			uni.request({
				url:_this.requesturl + '/txapi/shortvideo/index?key=' + _this.key + '&url=' + url,
				success:function(res){
					console.log(res)
					uni.hideLoading()
					_this.url = ''
					_this.disabled = true
					if(res.data.newslist[0].coverimg){
						_this.coverimg = res.data.newslist[0].coverimg
						_this.explain = res.data.newslist[0].explain
						_this.videourl = res.data.newslist[0].videourl
						_this.showsavebutton = true
					}else{
						_this.coverimg = ''
						_this.explain = ''
						_this.videourl = ''
						_this.showsavebutton = false
						uni.showToast({
							title:'操作失败，请检查链接地址是否正确',
							icon:'none'
						})
					}
					
				}
			})
		},
		playvideofn:function(){
			this.playvideo = true
		},
		closevideofn:function(){
			this.playvideo = false
		},
		savevideofn:function(){
			console.log('保存视频')
			var _this = this
			var url = _this.videourl
			if(url.indexOf('https') == -1){
				url.replace(/http/, "https")
			}
			console.log(url)
			uni.downloadFile({
				url: url,
				success: (res) => {
					console.log(res)
					if (res.statusCode === 200) {
						console.log('下载成功');
						uni.showToast({
							title:'下载成功'
						})
					}
				},
				fail:(data)=>{
					console.log(data)
					uni.showToast({
						title:'下载失败'
					})
				}
			});
		}
	}
}
